<template>
  <Loading :active="isLoading" />
  <Swiper :="swiperOption" class="alike ps-0">
    <SwiperSlide class="alike_list" v-for="item in randomProducts" :key="item">
      <div @click="goProduct(item.id)" class="alike_list_img stretched-link">
        <img :src="item.imageUrl" :alt="item.title" />
      </div>
      <div class="alike_list_baking" v-if="item.baking">
        {{ item.baking }}
      </div>
      <div class="alike_list_baking_1" v-else></div>
      <div class="alike_list_title">
        {{ item.title }}
      </div>
      <div class="alike_list_desc">
        {{ item.decription }}
      </div>
      <div class="alike_list_price">
        <div class="alike_list_price_1" v-if="item.price === item.origin_price">
          {{ $filters.currency(item.origin_price) }}  / {{ item.unit }}
        </div>
        <del class="alike_list_price_2" v-if="item.price !== item.origin_price">
           {{ $filters.currency(item.origin_price) }} 
        </del>
        <div class="alike_list_price_2" v-if="item.price !== item.origin_price">
           {{ $filters.currency(item.price) }}  / {{ item.unit }}
        </div>
      </div>
      <div class="d-flex  mt-2">
        <button
          :disabled="loadingStatus.loadingItem === item.id"
          @click="addToCart(item.id)"
          type="button"
          class="btn btn-secondary w-100"
        >
          <i
            class="spinner-border spinner-border-sm"
            v-if="loadingStatus.loadingItem === item.id"
          ></i>
          
        </button>
      </div>
    </SwiperSlide>
  </Swiper>
</template>

<script>
import { apiGetProduct, apiAddCart } from '@/methods/api';
import emitter from '@/methods/eventBus';
import { Swiper, SwiperSlide } from 'swiper/vue';
import 'swiper/swiper.scss';
import SwiperCore, { Autoplay, Navigation } from 'swiper/core';

SwiperCore.use([Navigation, Autoplay]);

function getRandomInt(max) {
  return Math.floor(Math.random() * max);
}

export default {
  inject: ['swalert'],
  components: {
    Swiper,
    SwiperSlide,
  },
  data() {
    return {
      product: {},
      products: [],
      randomProducts: [],
      loadingStatus: { loadingItem: '' },
      isLoading: false,
      id: '',
      swiperOption: {
        slidesPerView: 2,
        spaceBetween: 10,
        navigation: true,
        breakpoints: {
          992: {
            slidesPerView: 4,
            spaceBetween: 20,
          },
          600: {
            slidesPerView: 3,
            spaceBetween: 20,
          },
        },
        autoplay: {
          delay: 4000,
          disableOnInteraction: false,
        },
      },
    };
  },
  mounted() {
    this.id = this.$route.params.id;
    this.getProduct(this.id);
  },
  watch: {
    $route() {
      this.randomProducts = [];
      this.id = this.$route.params.id;
      this.getProduct(this.id);
    },
  },
  emits: ['go-page'],
  methods: {
    getProduct(id) {
      apiGetProduct(id)
        .then((res) => {
          this.product = res.data.product;
          this.getProducts();
        })
        .catch((err) => {
          this.swalert('error', ` ${err.message}`);
        });
    },
    getProducts() {
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/products/all`;
      this.$http
        .get(api)
        .then((res) => {
          this.products = res.data.products;
          this.getAlike();
        })
        .catch((err) => {
          this.swalert('error', ` ${err.message}`);
        });
    },
    goProduct(id) {
      this.$router.push(`/product/${id}`);
      this.$emit('go-page', id);
    },
    addToCart(id, qty = 1) {
      this.isLoading = true;
      const cart = { product_id: id, qty };
      apiAddCart({ data: cart })
        .then((res) => {
          if (res.data.success) {
            this.isLoading = false;
            emitter.emit('updata-cart');
            emitter.emit('render-cart');
            this.swalert('success', '');
          } else {
            this.isLoading = false;
            this.swalert('error', ` ${res.data.message}`);
          }
        })
        .catch((err) => {
          this.swalert('error', ` ${err.message}`);
        });
    },
    getAlike() {
      let category;

      // 
      if (this.product === undefined) {
        category = '';
        const filterProducts = this.products.map((item) => item);
        const maxSize = filterProducts.length < 6 ? filterProducts.length : 6;
        const arrSet = new Set([]);
        for (let index = 0; arrSet.size < maxSize; index + 1) {
          const num = getRandomInt(filterProducts.length);
          arrSet.add(num);
        }
        arrSet.forEach((i) => {
          this.randomProducts.push(filterProducts[i]);
        });
        return;
      }

      // 
      category = this.product.category;
      // 
      const { title } = this.product;
      const filterProducts = this.products.filter((item) => {
        if (item.title === title) {
          return false;
        }
        return item.category === category;
      });

      // 
      // const filterProducts = this.products.filter((item) => item.category === category);
      const maxSize = filterProducts.length < 4 ? filterProducts.length : 4;
      const arrSet = new Set([]);
      getRandomInt();
      for (let index = 0; arrSet.size < maxSize; index + 1) {
        const num = getRandomInt(filterProducts.length);
        arrSet.add(num);
      }
      arrSet.forEach((i) => {
        this.randomProducts.push(filterProducts[i]);
      });
    },
  },
};
</script>

